import React, { Component } from 'react';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Snackbar from '@material-ui/core/Snackbar';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { height } from '@material-ui/system';
import Button from '@material-ui/core/Button';
import SnackBarAlert from '../SnackBarAlert/snackBarAlert'
import './myblogs.css'
import url from '../../../../globalModal/url';


 export class MyBlogsComponent extends Component {

    constructor(props) {
        super(props)
        console.log(this.props)
        this.state = {
            data: [],    //array of blogs
            expanded: null,        //param to open expansion panel
            openErrorSnack: false, //param to open snackBar
        }
    }

    componentDidMount() {
        axios.get(`http://${url.base.url}/${this.props.match.path.split('/')[1]}/getMyBlogs`)
            .then(response => {
                console.log(response);
                if (response.data) {
                    this.setState({ data: response.data.result});
                    //this.setState({openInfoSnack:true})
                }
                else {
                    //show the msg in dialogBox
                }
            })
            .catch(err => {
                console.log(err)
                this.setState({openErrorSnack:true})
            })
    }

    handleChange = panel => (event, isExpanded) => {
        if (isExpanded) {
            this.setState({ expanded: panel })
        }
        else {
            this.setState({ expanded: null })
        }
    };

    edit(blogId) {
        //console.log(blogId)
        this.props.history.push(`/${this.props.match.path.split('/')[1]}/edit/${blogId}`)
    } 

    read(blogId) {
        this.props.history.push(`/blog/${blogId}`);
        
    }

    render() {
        const classes ={
            root: {
                width: '100%',
            },
            heading: {
                fontSize: 17,
                flexBasis: '33.33%',
                flexShrink: 0,
            },
            secondaryHeading: {
                fontSize: 15,
                color: 'black',
            }
        }



        return (
            <div className="myblcs"> 
                <div className='heading'>
                MY BLOGS
                </div>
                {this.state.data.map((content, i) => (
                    <div className="expanel" key={i}>
                        <ExpansionPanel expanded={this.state.expanded === i}
                            onChange={this.handleChange(i)}>
                            <ExpansionPanelSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1bh-content"
                                id="panel1bh-header"
                            >
                                <Typography style={{ 'line-height': '3rem' }} style={classes.heading}> {i+1} &nbsp; {content.topic}</Typography>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="buttonDiv">
                                    <Button color="secondary" variant="contained"
                                        onClick={() => this.edit(content.blogId)} style={{ margin: '20px' }}>
                                        Edit Article
                                    </Button>
                                    <Button color="primary" variant="contained"
                                        onClick={() => this.read(content.blogId)} style={{ margin: '20px' }}>
                                        Read Article
                                    </Button>
                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>
                    </div>
                ))}
                {this.state.openErrorSnack?<SnackBarAlert message="Some Error Occured" severity="error"/>:null}
            </div>
        )
    }
}

export default MyBlogsComponent;



