import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  linkh:{
    color:"white"
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

export default function DataBlog(props) {
  const classes = useStyles();
  
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>

       {props.list.map((user,key)=>{
           return <div className="expanel" key={user.blogId} >
      <ExpansionPanel  expanded={expanded === key} onChange={handleChange(key)} >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.heading}>{key+1}. &nbsp;{user.topic}</Typography>
       
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
        <div style={{marginLeft:'10px'}}>
 <Link   to={'/teacher/edit/'+user.blogId}>  <Button color="primary"  variant="contained" >Edit/Check</Button></Link>
        </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>
</div>
      })}
      
    
    </div>
  );
}
