
import React,{useState,useEffect,useRef} from 'react';
import axios from 'axios'
import url from '../../../globalModal/url';
import {useSnackbar}from 'notistack';
export default function AuthorDisp(props) {
  
 // console.log(props);
 const isLoaded=useRef(null);
 const { enqueueSnackbar, closeSnackbar } = useSnackbar();
 const [user,setUser]= useState(props.id)

 function backendCall(){
    let studenturl=url.getStudentDetailsUrl+"?id="+user;
    let teacherurl =url.getTeacherAuthorData+"?id="+user;
     axios.get(studenturl).then((data=>{
        if(data.data.status==='S'){
         //   console.log(data)
            setUser(data.data.result.name);
        }
    })).catch(err=>{
        axios.get(teacherurl).then((data=>{
            if(data.data.status==='S'){
             //   console.log(data)
                setUser(data.data.result.name);
            }
        })).catch(err=>{
          enqueueSnackbar('No User Found',{
              variant:'error'
          })
        })
    })

  
    
   
 }

  useEffect(()=>{
    backendCall();
  
    return ()=>{
   
    }
    },[])

return (
<span>
{user}
</span>
        

)
}

