import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
// import AppBar from '@material-ui/core/AppBar';
// import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
//import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
//import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
// import InboxIcon from '@material-ui/icons/MoveToInbox';
//import MailIcon from '@material-ui/icons/Mail';
import Button from '@material-ui/core/Button';
import AdminHeader from './adminHeader';
import {Link} from 'react-router-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Categories from './categoryCrud/categories';
import EmpVerification from './teacherVerification/empVerification';

const drawerWidth = 240;

const useStyles ={
  root: {
    display: 'flex',
  },

  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    backgroundColor:'inherit',
    padding: '100px'
  },
  options:{
      textDecoration:'none',
      fontSize:'15px',
      fontWeight:600,
      color:'black',
    
      
  }
};

class Hompage extends React.Component{
constructor(){
super()
this.state={
    list:[
    {'name':'Categories',url:'/admin/categories'},
    {'name':'Make Checker',url:'/admin/EmpVerification'}
]
}

}
   
componentDidMount(){
    this.setState(this.state);
}
  
    render(){
        const { classes } = this.props;
       
   

  return (
    <div className={classes.root}>
      <CssBaseline />
    <AdminHeader history={this.props.history}/>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"
      >
     
        <Divider />
        <List>
          {this.state.list.map((obj, index) => (
               <Link to={obj.url} key={obj.name} className={classes.options}>
            <ListItem >
            <Button color="primary" variant="contained" style={{width:'100%'}}>{obj.name}</Button>
            </ListItem>
            </Link>
          ))}
        </List>
        <Divider />
      
      </Drawer>
      <main className={classes.content}>
      <Switch>
                        <Route path="/admin" exact component={Categories}/>
                        <Route path="/admin/categories" component={Categories} />
                        <Route path="/admin/EmpVerification" component={EmpVerification}/>
                        
                    </Switch>
      </main>
    </div>
  );
    }
    
}

Hompage.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(useStyles)(Hompage);
