import React from 'react';
import axios from 'axios';
import url from '../../../globalModal/url';
import HomeHeader from '../HomeModule/home/homeHeader';
import BlogContent from './blogContent';
import UserProfile from './user';
import TeacherProfile  from './teacher';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import CommentSection from '../studentModule/comment/commentSection';
import  StudentInterceptor, { StudentHttpClient } from '../sharedModule/Interceptors/StudentInterceptor';
import StarRating from '../sharedModule/startRating/star-rating';
import {withSnackbar}from 'notistack';
import './blogPage.css';
class BlogPage  extends React.Component{

    constructor(props){
        super(props);
        this.state={
            blog:null,
            writer:null,
            teacher:null,
            comments:[],
            addcomment:null
        }

    }
   

    getComments(){
        let commenturl=url.getComments+"?id="+this.props.match.params.id;
        axios.get(commenturl).then(res=>{
            let data=res.data;
            // console.log(data);
            if(data.status=='S'){
                this.setState({
                    ...this.state,
                    addcomment:null,
                    comments:data.comments
                })
            }
        }).catch(err=>{
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
        })
    
    }

comment(){
    
    axios.get(url.getIdoflogInUser).then(data=>{
    console.log(data);
        StudentHttpClient.post(url.comment,{blogId:this.props.match.params.id,
            description:this.state.addcomment,userId:data.data.result.id}).then(res=>{
            let data=res.data;
            if(data.status=='S'){
               this.getComments()
            }else{
             
                    this.props.enqueueSnackbar('Error Occured',{ 
                      variant: 'error'
                    })
            }
        }).catch(err=>{
            console.log(err);
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
        })
    }).catch(err=>{
        if(err.response!=null){
            this.props.enqueueSnackbar(err.response.data.message,{ 
              variant: 'error'
          });
           }else{
            this.props.enqueueSnackbar(err.message,{ 
              variant: 'error'
          })
        }
    })
    // console.log(this.state.addcomment);
 
}

handleComment=e=>(event)=>{
    this.setState({
       addcomment:event.target.value
    })}


 componentDidMount(){
    console.log(this.props)
    let id= this.props.match.params.id
    if(id!=null){
      let blogurl=  url.getBlogUrl+"?id="+id;
axios.get(blogurl).then(data=>{
    if(data.data.status==='S'){
        let blogdata={...data.data.data.blogTemplate,...data.data.data.blogTopic}
        console.log(blogdata);
        blogdata.content.sort((a,b)=>  a.priority - b.priority  )
     this.setState({
         ...this.state,
         blog:blogdata

     })
     let studenturl=url.getStudentDetailsUrl+"?id="+blogdata.userId;
     axios.get(studenturl).then(data=>{
         if(data.data.status==='S'){
             console.log(data)
             this.setState({
                 ...this.state,
                 blog:this.state.blog,
                 teacher:this.state.teacher,
                 writer:data.data.result
             })
         }
     }).catch(err=>{
        let writerurl=url.getTeacherAuthorData+"?id="+blogdata.userId;
        axios.get(writerurl).then(data=>{
            if(data.data.status==='S'){
                console.log(data)
                this.setState({
                    ...this.state,
                    writer:data.data.result
                })
            }
     })
    })
     let teacherurl=url.getTeacherInfo+"?id="+blogdata.blogId;
     axios.get(teacherurl).then(data=>{
        if(data.data.status==='S'){
           
            this.setState({
                ...this.state,
                blog:this.state.blog,
                teacher:data.data.result,
                writer:this.state.writer
            })
        }
    })

    this.getComments();


    }
}).catch(err=>{
  //  console.log(err);
    if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
})
    }else{
        this.props.enqueueSnackbar('Invalid Id',{
            variant:'error'
        })
    }
 }
 
 
    render(){

    
        return (
            <div >
                <div className="upperspace">
                    <HomeHeader/>
                </div>
         <div className="roothereBlogMain">
     {this.state.blog==null?null:
     <div>
         <div className="topicheading">
       <h1> {this.state.blog.topic}</h1>
       </div>
       {this.state.writer!=null?
       <div className="userProfile">
          <UserProfile user={this.state.writer}/>
           </div>
           :null
       }
       <div className="imgcontainerBlogPage">
           <img src={this.state.blog.thumbnail }/>
           </div>
        
        <BlogContent content={this.state.blog}></BlogContent>


        {this.state.teacher!=null?
       <div className="userProfile">
           <div style={{textAlign:'left',marginBottom:'10px'}}> Checked By:</div>
          <TeacherProfile user={this.state.teacher}/>
           </div>
           :null
       }
         <StarRating blogId={this.state.blog.blogId} rating={this.state.blog.stars}/>

       <div style={{
             margin: '4% 4% 4% 4%',
             padding:'2%',
             boxShadow: "0px 0px 5px 5px lightgray"
       }}>
           <div style={{
               fontSize:'28px',
               fontWeight:'600',
               textAlign: 'left',
             
           }
           }>
               Comments ({this.state.comments.length})
               </div>
      {
          this.state.comments.length!=0 ?
          this.state.comments.map((comment,key)=>{
           return  <div key={key}>
            <CommentSection comment={comment} />

                  </div>
          }):null
      }
 
        <div>
        <TextField
                  id="standard-multiline-static"
                  label="Enter Comment"
                  multiline
                  className="textField"
                  rows="4"
                  onChange={this.handleComment()}
                  margin="normal"
              />
            <Button color="primary" variant="outlined" onClick={()=>{this.comment()}}>Comment</Button>
            </div>
      </div>
      </div>
     }
            </div>
            </div>
        )
    }
}


export default withSnackbar(BlogPage);