import React from 'react';
import {TeacherHttpClient} from '../../sharedModule/Interceptors/TeacherInterceptor'
import url from '../../../../globalModal/url';
 import DataBlog from './data';
 import CircularProgress from '@material-ui/core/CircularProgress';
 import {withSnackbar} from 'notistack';
 class MyBlogs extends React.Component{
 
constructor(){
    super();
    this.state={
        myblogs:[]
    }
}
componentDidMount(){
    this.getMyBlogs();
    }

insert(conarr){

let arr=conarr.map((obj)=>{
    return {
    stars:obj.stars,
_id: obj._id,
catId: obj.catId,
topic: obj.topic,
thumbnail: obj.thumbnail,
published: obj.published,
blogId:obj.blogId,
updatedAt: obj.updatedAt,
userId:obj.userId
    }
})
this.setState({
    ...this.state,
    myblogs:arr
})
 
}

getMyBlogs(){
TeacherHttpClient.get(url.getTeacherBlogs).then(response=>{
  if(response.data.status=='S'){
      console.log(response.data);
  
 this.insert(response.data.result);
  }

}).catch(err=>{
    if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
})


}



render(){

    const classes={
        dialoghere:{
           
           
            marginTop:"10%"
        },
        herhead:{
            fontSize:"30px",
            textAlign:"left",
            padding:"30px",
            color:"white"
        }
    }
    
    return(
       <div>
           <div style={classes.herhead} >
                MY BLOGS
           </div>
         { this.state.myblogs.length!=0?  <DataBlog list={this.state.myblogs} />
         :
         <div style={classes.dialoghere} >
           <CircularProgress color="secondary" />
         </div>
         }
       </div>

    )

    
}

}

export default  withSnackbar(MyBlogs);