import React from 'react';
import url from '../../../../globalModal/url';
import axios from 'axios';
import HomeHeader from '../../HomeModule/home/homeHeader';
import Container from '@material-ui/core/Container';
import BlogDisp from '../../sharedModule/blogDisplay';
import PopularList from '../../HomeModule/home/popularContent';
import { textAlign } from '@material-ui/system';
import { withSnackbar } from 'notistack';
import './category.css';

 class CategoryBlog extends React.Component{

    state={
        blog:[],
        populartopics:[]
    }
    constructor(){
        super()
    }

    getTopicOfBlog=(id)=>{
        let url1=url.getBlogOfCategory+"?catId="+id;
        axios.get(url1).then(data=>{
            if(data.data.status==='S'){
             console.log(data)
              this.setState({
      blog:data.data.data
              }) 
            }}).catch(err=>{
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
             })
      }

      getpopularTopics(){
        let featuredId="dkGQ6uNpCT";
        let url1=url.getBlogOfCategory+"?catId="+featuredId;
        axios.get(url1).then(data=>{
            if(data.data.status==='S'){
             let arr=  (data.data.data).sort((a,b)=>{
                 return b.stars.value-a.stars.value
             });
              
             this.setState((prevstate)=>({
                blog:prevstate.blog,
                populartopics:arr
            }))
            }
            }).catch(err=>{
                console.log(err);
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
            })
    }
    componentDidMount(){
        let id=this.props.match.params.id;
        console.log(id);
        if(id!=null){
          this.getTopicOfBlog(id);
          this.getpopularTopics();
        }
      else{
          this.props.enqueueSnackbar('Invalid category',{
              variant:'error'
          });
      }
    }
    
    componentWillReceiveProps(newprops){
          let id=newprops.match.params.id;
          console.log(id);
          if(id!=null){
            this.getTopicOfBlog(id);
            this.getpopularTopics();
          }
        else{
            this.props.enqueueSnackbar('Invalid category',{
                variant:'error'
            });
        }
      }

    render(){

     
        return (
            <div>
           <Container maxWidth="lg">
          <div >
       <HomeHeader/>
       </div>
       <div className="contentContainercategory">
       <div className="headingcategory">
        {localStorage.getItem('category')!=null?
        `${localStorage.getItem('category')}`:'Blogs'
        }
       </div>
       <div className="flexcontainercategory">
       <div className="blogDispcategory">
       <BlogDisp blog={this.state.blog}/>
       </div>
       <div className="popularcategory">
       <PopularList popularitem={this.state.populartopics}></PopularList>
       </div>
       </div>
       </div>
            </Container>
            </div>
        )
    }

}

export default withSnackbar(CategoryBlog);