import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import Headercomp from './HeaderComponent/headercomp';
import SnackBarAlert from '../../sharedModule/SnackBarAlert/snackBarAlert'
import './signupComp.css';
import url from '../../../../globalModal/url';

export class signupComp extends Component {
    initialState = {
        name: '',
        nameError: '',
        email: '',
        emailError: '',
        password: '',
        passwordError: '',
        phoneno:'',
        phonenoError:'',
        documentLink:'',
        documentLinkError:'',
        openErrorSnack: false,
        openSuccessSnack: false
    }

    constructor(props) {
        super(props);
        this.state = this.initialState;
    }

    //handle the submit disable
    validate() {
        if (this.state.name.length >= 4&&this.state.email.includes('@') 
          && this.state.password.length >= 6 && /^[6-9]\d{9}$/i.test(this.state.phoneno)
           && this.state.documentLink.length>2) {
            return true;
        }
        else {
            return false;
        }
    }

    //handle the name validations
    handleName = input => event => {
        this.setState({ name: event.target.value });
        if (event.target.value.length < 4) {
            this.setState({ nameError: 'Username atleast 4 characters' })
        }
        else {
            this.setState({ nameError: '' })
        }

    }

    handlePhoneNo = input => event => {
        this.setState({ phoneno: event.target.value });
        if (!/^[6-9]\d{9}$/i.test(event.target.value)) {
            this.setState({ phonenoError: 'Phone No format incorrect' })
        }
        else {
            this.setState({ phonenoError: '' })
        }

    }
    //handle Email Validations
    handleEmail = input => event => {
        this.setState({ email: event.target.value });
        if (!event.target.value.includes('@')) {
            this.setState({ emailError: 'Email should be proper' })
        }
        else {
            this.setState({ emailError: '' })
        }

    }
    handlePassword = input => event => {
        this.setState({ password: event.target.value });
        if (event.target.value.length < 6) {
            this.setState({ passwordError: 'Password atleast 6 characters' })
        }
        else {
            this.setState({ passwordError: '' })
        }

    }
    handleDocumentLink = input => event => {
        this.setState({ documentLink: event.target.value });
        if (event.target.value.length < 3) {
            this.setState({ documentLinkError: 'Enter valid link' })
        }
        else {
            this.setState({ documentLinkError: '' })
        }

    }

    submit() {
        //send state to backend
        if (this.validate()) {
            console.log(this.state);
            let obj={
                    ...this.state,
                    link: url.teacheremailverifyLinkUrl,
                    data: {
                        subject:'Email Verification'
                    },
                    action: 'em'
                }
                
            axios.post(url.teacherSignup, obj)
                .then(response => {
                    console.log(response);
                    this.setState({openSuccessSnack:true})
                })
                .catch(err => {
                    console.log(err)
                    this.setState({openErrorSnack:true})
                })
        }
        else{
            this.setState({openErrorSnack:true})
        }
    }

    render() {
        return (
            <div>
                <Headercomp />
                <div className={'signupcontainer'}>
                <div className={'bannerhead'}>
                   TEACHER SIGNUP
                </div>
                 <div className={'banner'}>
                    Sign Up As Teacher

                 </div>
                 <div style={{fontSize:'12px',color:'red'}}>
                     *Note: After documents verification only you will be allowed to login
                 </div>
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    label="Username"
                    placeholder="Username"
                    error={this.state.nameError}
                    className="inputField"
                    helperText={this.state.nameError}
                    onChange={this.handleName('name')}
                />
                </div>
                <br />
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter Email(College)"
                    label="Email"
                    error={this.state.emailError}
                    className="inputField"
                    helperText={this.state.emailError}
                    onChange={this.handleEmail('email')}
                />
                </div>
                <br />
               
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter Password"
                    label="Password"
                    type="password"
                    error={this.state.passwordError}
                    className="inputField"
                    helperText={this.state.passwordError}
                    onChange={this.handlePassword('password')}
                />
                </div>
                <br />
                
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter 10 digit mobile no"
                    label="Mobile No"
                    error={this.state.phonenoError}
                    className="inputField"
                    helperText={this.state.phonenoError}
                    onChange={this.handlePhoneNo('phoneno')}
                />
                </div>
                <br />
               
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter Link to your qualification/degree"
                    label="documentLink"
                    error={this.state.documentLinkError}
                    className="inputField"
                    helperText={this.state.documentLinkError}
                    onChange={this.handleDocumentLink('documentLink')}
                />
                </div>
                <br />
                <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                    style={{ margin: '20px' }}>
                    Submit
                </Button>
                {this.state.openErrorSnack ? <SnackBarAlert message="Some Error Occured" severity="error" /> : null}
                {this.state.openSuccessSnack ? <SnackBarAlert message="SignUp successful" severity="success" /> : null}
          </div>
            </div>
        )
    }
}

export default signupComp
