import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import Headercomp from '../HeaderComponent/headercomp'
import url from '../../../../globalModal/url';
import {Link} from 'react-router-dom';
import { StudentHttpClient } from '../../sharedModule/Interceptors/StudentInterceptor';
import './login.css';
import PasswordRecover from '../passwordOptions/passwordrecover';
import { exportDefaultSpecifier } from '@babel/types';
import {withSnackbar} from 'notistack';
import { CONSTANTS } from '../../../../globalModal/constants';
export class LoginComponent extends Component {
    initialState = {
        email: '',
        emailError: '',
        password: '',
        passwordError: ''

    }

    constructor(props) {
        super(props);
        this.state = this.initialState
    }

    componentDidMount(){
        this.checkLogin();
        localStorage.setItem('passpage','ST');
    }

 checkLogin(){
    StudentHttpClient.get(url.checkLogin).then(response => {
        if (response.data.message) {
            console.log(response.data.message);
            this.setState({ jwtVerified: true })
            this.props.history.push('/student/new');
        }
    }).catch(err => {
        console.log(this.props)
        localStorage.removeItem('studentToken')
        this.props.history.push('/login')
    })
 }

    //handle the submit disable
    validate() {
        if (this.state.email.includes('@') && this.state.password.length >= 6) {
            return true;
        }
        else {
            return false;
        }
    }


    handleEmail = input => event => {
        this.setState({ email: event.target.value });
        if (!event.target.value.includes('@')) {
            this.setState({ emailError: 'Email should be proper' })
        }
        else {
            this.setState({ emailError: '' })
        }
    }

    handlePassword = input => event => {
        this.setState({ password: event.target.value });
        if (event.target.value.length < 6) {
            this.setState({ passwordError: 'Password atleast 6 characters' })
        }
        else {
            this.setState({ passwordError: '' })
        }
    }

    recoverpass=(value)=>{

  if(value!=null){
    let obj={
        email:value,
        type:'ST',
        link: url.passwordMake,
        data: {
            subject:'Recover Password'
        },
        action: 'em'
     };

      axios.post(url.recoverStudentPass,obj).then(data=>{
    this.props.enqueueSnackbar('Recovery Email Sent , Please Check email for further process',{
        variant:'success'

    })


      }).catch(err=>{
        if(err.response!=null){
            this.props.enqueueSnackbar(err.response.data.message,{ 
              variant: 'error'
          });
           }else{
            this.props.enqueueSnackbar(err.message,{ 
              variant: 'error'
          })
           }
      })
  }


    }


    submit() {
        //send state to backend
        if (this.validate()) {
            console.log(this.state);
            axios.post(url.studenLoginurl, this.state)
                .then(response => {
                    console.log(response);
                    //alert(response.data.msg)
                    if (response.data.logged) {
                        localStorage.setItem('studentToken', response.data.token)
                        this.props.history.push("/student/new")
                    }
                })
                .catch(err => {
                    console.log(err)
                    if(err.response!=null){
                        this.props.enqueueSnackbar(err.response.data.message,{ 
                          variant: 'error'
                      });
                       }else{
                        this.props.enqueueSnackbar(err.message,{ 
                          variant: 'error'
                      })
                       }
                })
        }
        else {
          
                this.props.enqueueSnackbar('Please Fill The Complete Form...',{ 
                  variant: 'error'
              })
               
        }
    }

    render() {
        return (
            <div>
                <Headercomp />

                <div className={'logincontainer'}>
                <div className={'bannerhead'}>
                    STUDENT LOGIN
                </div>
                <div className={'banner'}>
                    Already a member? if not <Link to='/signup'>SignUp</Link>
                </div>
             
                <div className={'eachel'} >
                <TextField style={{width:'100%'}}
                    placeholder="Enter Email(College)"
                    label="Email"
                    error={this.state.emailError}
                    className="inputField"
                    helperText={this.state.emailError}
                    onChange={this.handleEmail('email')}
                />
                <br />
                </div>
                <div className={'eachel'} >
                <TextField style={{width:'100%'}}
                    placeholder="Enter Password"
                    label="Password"
                    type="password"
                    error={this.state.passwordError}
                    className="inputField"
                    helperText={this.state.passwordError}
                    onChange={this.handlePassword('password')}
                />
                <br />
                </div>
                <div>
                <PasswordRecover submit={this.recoverpass}></PasswordRecover>
                </div>
                <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                    style={{ margin: '20px' }}>
                    Submit
                </Button>
                </div>
            </div>
        )
    }
}

export default withSnackbar(LoginComponent);
