import React from 'react';
import axios from 'axios';
import url from '../../../../globalModal/url';
//import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { deepPurple } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import {Link} from 'react-router-dom';
import SearchIcon from '@material-ui/icons/Search';
import {withSnackbar} from 'notistack';
import MenuDrawerNav from '../../BlogModule/menuNavDrawer';
import './homepage.css';
import CustomMenu from '../../studentModule/Header/CustomMenu/customMenu';
import TeacherCustomMenu  from '../../teacherModule/Header/CustomMenu/customMenu';
class HomeHeader extends React.Component{
  

  constructor(props){
    super(props);
    this.state={
      categories:[]
    }
  
}
  getCategories(){
    axios.get(url.getAllCategory).then(data=>{
    //  console.log(data)
      if(data.data.status==='S'){
        console.log(data.data.result);
   this.setState({
     categories:data.data.result
   })
      }
    }).catch(err=>{
      if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
    })
  }

  storeIt(category){
    localStorage.setItem('category',category);
  }

componentDidMount(){
  this.getCategories();
 

}


  render(){
   
 
  return (
    <div className="headerheader">
       <div className="centerheader">
       <div >
       <Grid container justify="flex-start" alignItems="center">
  <Avatar className="purpleAvatarheader">EB</Avatar>
  <Grid className="titleheader" item xs={1} sm={1}>

   <Link to='/' className="linkheader">Eng Blog</Link>
   
    </Grid>
</Grid>

       </div>
       <div className="endheader">
       <div className="menuplace">
         <MenuDrawerNav categories={this.state.categories}></MenuDrawerNav>
         </div>
       <div className="rightSectionheader">
        <Link to="/search" className="linkheader"> <SearchIcon className="searchheader"/></Link>
         </div>
        
         <div className="rightSectionheader">
          <div > {localStorage.getItem('studentToken')!=null?
           <CustomMenu></CustomMenu>:
           localStorage.getItem('teacherToken')!=null?
           <TeacherCustomMenu></TeacherCustomMenu>:
           <Button   >
           <Link to={"/login"} className="linkheader" >
           Login
           </Link>
           </Button> 
        }
       
            </div>
         </div>

       </div>
       </div>

       <div className="flexcontainerheader">
         <div className="rootheader">
       <div className="gridcontainerheader">
         {this.state.categories.map((value)=>{
       return  <div className="gridheader" key={value.catId} >
      <Link to={`/category/${value.catId}`} className="linkheader"><Button onClick={()=>this.storeIt(value.catName)} className="buttonheader">{value.catName}</Button></Link>
     </div>
         })}
         </div>
       </div>
       </div>
    </div>
  );
}
}

export default withSnackbar(HomeHeader);
