import React from "react";
import {withSnackbar} from 'notistack';
import { Button, TextField } from "@material-ui/core";
import Axios from "axios";
import url from "../../../../globalModal/url";
import { CONSTANTS } from "../../../../globalModal/constants";
import './password.css';
class PasswordChange extends React.Component{
constructor(props){
    super(props);

    this.state={
        password:null,
        confirmpass:null,
        passwordError:false,
        passwordMessage:'',
        confirmpassError:false,
        confirmpassMessage:'',
        id:null,
        type:null
    }
}

componentDidMount(){
   let id= new URLSearchParams(this.props.history.location.search).get('verifyId');
   let type= new URLSearchParams(this.props.history.location.search).get('type');
   this.setState({
       ...this.state,
       id:id,
       type:type
   })
 
}

handlePass= input => event => {

    this.setState({ 
        [input]: event.target.value });
    if (event.target.value.length < 6) {
        this.setState({ 
          
                [`${input}Error`]:true,
           [`${input}Message`]: "Password atleast 6 characters" })
    }
    else {
        this.setState({ 
         
            [`${input}Error`]:false})
    }
}

validate() {
    if (this.state.password!=null && this.state.password.length >= 6 && this.state.password==this.state.confirmpass) {
        return true;
    }
    else {
        return false;
    }
}

setNewPass(){
    if(this.state.type==CONSTANTS.STUDENTPASS){
         Axios.post(url.setNewStudentPass,this.state).then(res=>{
             let data=res.data;
             if(data.status=='S'){
                this.props.enqueueSnackbar('Successfully Updated',{ 
                    variant: 'success'
                });
             }
         }).catch(err=>{
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
         })
    }else if(this.state.type==CONSTANTS.TEACHERPASS){

        Axios.post(url.setNewTeacherPass,this.state).then(res=>{
            let data=res.data;
            if(data.status=='S'){
               this.props.enqueueSnackbar('Successfully Updated',{ 
                   variant: 'success'
               });
            }
        }).catch(err=>{
           if(err.response!=null){
               this.props.enqueueSnackbar(err.response.data.message,{ 
                 variant: 'error'
             });
              }else{
               this.props.enqueueSnackbar(err.message,{ 
                 variant: 'error'
             })
              }
        })

    }else{
        this.props.enqueueSnackbar('Invalid Type of Request',{ 
            variant: 'error'
        });
    }

    }


submit=()=>{
   console.log(this.state);
   if(this.validate()){
      
     if(this.state.type!=null && this.state.id!=null){

this.setNewPass();
         

     }else{
        this.props.enqueueSnackbar('Id & Type not present',{ 
                variant: 'error'
            });
     }




   }else{
    this.props.enqueueSnackbar('Password and confirm password do not match',{ 
            variant: 'error'
        });
   }
}


render(){
    return (
        <div className="containermaster">
      <div className="passcontainer">

    <div className="headingcss">
    Fill Your New Password
    </div>
      <TextField style={{width:'100%'}}
                    placeholder="Enter New Password"
                    label="New Password"
                    error={this.state.passwordError}
                    className="inputField"
                    helperText={this.state.passwordMessage}
                    onChange={this.handlePass('password')}
                />

<TextField style={{width:'100%'}}
                    placeholder="Confirm New Password"
                    label="Confirm Password"
                   error={this.state.confirmpassError}
                    className="inputField"
                    helperText={this.state.confirmpassMessage}
                    onChange={this.handlePass('confirmpass')}
                    
                />
                <div className='btnsectionpass'>
       <Button disabled={this.state.password==null || this.state.confirmpass==null} color="primary" variant="contained" onClick={this.submit}>Submit</Button>
   </div>
   </div>
   
        </div>
    )
}

}

export default withSnackbar(PasswordChange);