import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import "./page.css"

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  info:{
fontSize:"12px",
textAlign:"left",
marginRight:"1rem"
  },

   desc:{
    fontWeight:600
   },

  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

export default function Verified(props) {
  const classes = useStyles();
console.log(props);
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>
   { props.verified!=null?<div>
  {props.verified.map((user,key)=>{
   
      return   <div key={user.id} style={{width:"100%"}}>
       
         <ExpansionPanel  expanded={expanded === key} onChange={handleChange(key)} >
       
      <ExpansionPanelSummary 
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography  className={classes.heading}>{user.name}</Typography>
        <Typography className={classes.secondaryHeading}>{user.phoneno}</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails>
        <div className={classes.info}>
          <p>
           <span className={classes.desc}>Id</span>: &nbsp;{user.id}
          </p>
          <p>
          <span  className={classes.desc}>Degree</span>: &nbsp;{user.documentLink}
       </p>
      
        </div>

     
       <Button  color="secondary" variant="contained" style={{marginTop:'1rem'}} onClick={()=>{props.reject(user.id)}}>Reject</Button>
      </ExpansionPanelDetails>

    </ExpansionPanel>
  </div>})}
</div>:null}
    </div>
  
  );
}
