import React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import {Link} from 'react-router-dom';
import url from '../../../globalModal/url';
import Toolbar from '@material-ui/core/Toolbar';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import './login.css';
import {withSnackbar} from 'notistack';
 class adminLogin extends React.Component{


    state={
        email:'',
        password:'',

    }

    
    handleChange=input=>event=>{
        this.setState({[input]:event.target.value});
        
    }

    submit(){
        axios.post(url.adminLoginurl,this.state)
        .then(response=>{
            console.log(response);
            //alert(response.data.msg)
            if(response.data.logged===true){
                this.props.enqueueSnackbar('Successfully Recieved',{ 
                    variant: 'success'
                });
                localStorage.setItem('adminToken',response.data.token);
                this.props.history.push("/admin")
            }
        })
        .catch(err=>{
           // console.log(err.response);
          //  console.log(err);
          if(err.response!=null){
            this.props.enqueueSnackbar(err.response.data.message,{ 
              variant: 'error'
          });
           }else{
            this.props.enqueueSnackbar(err.message,{ 
              variant: 'error'
          })
           }
        })
    }

    render(){
      
        const style={
            
            appBar: {
                width: '100%',
               
              },
            
            content:{
                paddingTop:'80px'
            },
            options:{
                textDecoration:'none',
                fontWeight:600,
                color:'white',
             
                
            }
        }
           
        return (
      <div>
          <div>
       <AppBar position="fixed" style={style.appBar}>
            <Toolbar>
              <Typography variant="h6" noWrap>
              <Link to='/' style={style.options}>
              EB </Link>   
              </Typography>
             
            </Toolbar>
          </AppBar>
                </div>

          <div className={'logincontaineradmin'}>
              <div className={'banner'}>
                  Admin Login
              </div>
          <div className={'eachel'}>
           <TextField style={{width:'100%'}}
                 placeholder="Enter Admin Email "
                 label="Email"
                 onChange={this.handleChange('email')}
                />
                <br/>
                </div>
                <div className={'eachel'} >
                <TextField style={{width:'100%'}} 
                 placeholder="Enter Password"
                 label="Password"
                 type="password"
                 onChange={this.handleChange('password')}
                />
                <br/>
                </div>
                <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                   style={{margin:'20px'}}>
                  Submit
                </Button>
          </div>

      </div>


        )
    }

}
export default withSnackbar(adminLogin)