import React from 'react';
//import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import url from '../../../../globalModal/url';
import axios from 'axios';
import {Link} from 'react-router-dom';
import AuthorDisp from '../../sharedModule/author';
import {withSnackbar} from 'notistack';
import './homepage.css';
class TopicDetails extends React.Component{
  constructor(){
   super();
   this.state={
     blog:[]
   }
  }

  
getTopicOfBlog=(id)=>{
  let url1=url.getBlogOfCategory+"?catId="+id;
  axios.get(url1).then(data=>{
      if(data.data.status==='S'){
        console.log(data)
        this.setState({
blog:data.data.data
        }) 
      }}).catch(err=>{
        if(err.response!=null){
          this.props.enqueueSnackbar(err.response.data.message,{ 
            variant: 'error'
        });
         }else{
          this.props.enqueueSnackbar(err.message,{ 
            variant: 'error'
        })
         }
       })
}
componentDidMount(){
 //console.log(this.props)
  this.getTopicOfBlog(this.props.listitem.catId)
}
       
render(){

 
  return (
    
    <div >
      <div>
 {this.state.blog.map((blog,key)=>(
 <div key={key} className="flexlist">
    <div className="imgspec" >
       <img src={blog.thumbnail}/>
       </div>
  <div className="texthere">
    <div className="headinglist">
      <Link to={`/blog/${blog.blogId}`} className="linkstyle">{blog.topic}</Link>
     </div>
     <div className="author" >
  By: <AuthorDisp id={blog.userId}/>
     </div>
     <div className="time">
{blog.createdAt}
     </div>
     </div>
    
  </div>


 ))}
     
   
    </div>
    </div>
  );
}
}

export default withSnackbar(TopicDetails);