import React,{Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import './signup.css'
import url from '../../../../globalModal/url';
import Headercomp from '../HeaderComponent/headercomp'
import {Snack} from '../SnackBarAlert/snackBarAlert';
import {withSnackbar}from 'notistack';
 class SignUpComponent extends Component {

    initialState = {
        name: '',
        nameError: '',
        rollno: '',
        rollnoError: '',
        email: '',
        emailError: '',
        password: '',
        passwordError: ''
    }

    constructor(props) {
        super(props);
        this.state = this.initialState;
    }

    //handle the submit disable
    validate() {
        if (this.state.name.length >= 4 && /^\d[a-z]\d{2}\/[a-z]{2,}\/\d{1,3}$/i.test(this.state.rollno)
           &&this.state.email.includes('@') && this.state.password.length >= 6) {
            return true;
        }
        else {
            return false;
        }
    }

    //handle the name validations
    handleName = input => event => {
        this.setState({ name: event.target.value });
        if (event.target.value.length < 4) {
            this.setState({ nameError: 'Username atleast 4 characters' })
        }
        else {
            this.setState({ nameError: '' })
        }

    }

    handleRollNo = input => event => {
        this.setState({ rollno: event.target.value });
        if (!/^\d[a-z]\d{2}\/[a-z]{2,}\/\d{1,3}$/i.test(event.target.value)) {
            this.setState({ rollnoError: 'Rollno format incorrect' })
        }
        else {
            this.setState({ rollnoError: '' })
        }

    }
    //handle Email Validations
    handleEmail = input => event => {
        this.setState({ email: event.target.value });
        if (!event.target.value.includes('@')) {
            this.setState({ emailError: 'Email should be proper' })
        }
        else {
            this.setState({ emailError: '' })
        }

    }
    handlePassword = input => event => {
        this.setState({ password: event.target.value });
        if (event.target.value.length < 6) {
            this.setState({ passwordError: 'Password atleast 6 characters' })
        }
        else {
            this.setState({ passwordError: '' })
        }

    }

    submit() {
        //send state to backend
        if (this.validate()) {
            console.log(this.state);
            let obj={
                ...this.state,
                link: url.studentemailverifyLinkUrl,
                data: {
                    subject:'Email Verification'
                },
                action: 'em'
            }
            axios.post(url.studentSignupurl, obj)
                .then(response => {
                    console.log(response);
                    alert(response.data.msg);
                })
                .catch(err => {
                    console.log(err)
                    if(err.response!=null){
                        this.props.enqueueSnackbar(err.response.data.message,{ 
                          variant: 'error'
                      });
                       }else{
                        this.props.enqueueSnackbar(err.message,{ 
                          variant: 'error'
                      })
                       }
                })
        }
        else{
            
                this.props.enqueueSnackbar('Please Fill The Complete Form...',{ 
                  variant: 'error'
              })
               }
       
    }

    render() {
        return (
            <div>
                <Headercomp />
                <div className={'signupcontainer'}>
                <div className={'bannerhead'}>
                STUDENT SIGNUP
                </div>
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    label="Enter Username"
                    placeholder="Username"
                    error={this.state.nameError}
                    className="inputField"
                    helperText={this.state.nameError}
                    onChange={this.handleName('name')}
                />
                <br />
                </div>
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Format-2K16/EE/076"
                    label="Roll No"
                    error={this.state.rollnoError}
                    className="inputField"
                    helperText={this.state.rollnoError}
                    onChange={this.handleRollNo('rollno')}
                />
                <br />
</div>
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter Email(College)"
                    label="Email"
                    error={this.state.emailError}
                    className="inputField"
                    helperText={this.state.emailError}
                    onChange={this.handleEmail('email')}
                />
                <br />
                </div>
                <div className={'eachel'}>
                <TextField style={{width:'100%'}}
                    placeholder="Enter Password"
                    label="Password"
                    type="password"
                    error={this.state.passwordError}
                    className="inputField"
                    helperText={this.state.passwordError}
                    onChange={this.handlePassword('password')}
                />
                <br />
                </div>
                <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                    style={{ margin: '20px' }}>
                    Submit
                </Button>
                </div>
            </div>
        )
    }
}


export default withSnackbar(SignUpComponent);