import React from 'react';
import {TeacherHttpClient} from '../../sharedModule/Interceptors/TeacherInterceptor'
import url from '../../../../globalModal/url';
 import NewBlogs from './newBlogs';
import {withSnackbar}from 'notistack';
class listBlogs extends React.Component{

constructor(){
    super();
    this.state={
        blogs:[]
    }
}
getBlogs(){
TeacherHttpClient.get(url.getNonPublishedNewBlogs).then(response=>{
  if(response.data.status=='S'){
      console.log(response);
 
  this.setState({
      ...this.state,
      blogs:response.data.blogs
  });
  }

}).catch(err=>{
    if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
})


}

componentDidMount(){
this.getBlogs();
}

render(){

    const classes={
        dialog:{
            backgroundColor:'white',
            padding:'30px',
            borderRadius:'35px',
            fontWeight:'600',
            boxShadow:'0px 0px 5px 5px lightgray',
            fontSize:'20px',
            margin:'auto'
        }
    }
    
    return(
       <div>
         { this.state.blogs!=null && this.state.blogs.length!=0?  <NewBlogs list={this.state.blogs} />:
         <div style={classes.dialog} >
             No Non Published articles found
         </div>
         }
       </div>

    )

    
}

}

export default withSnackbar(listBlogs);