import React, { Component } from 'react';
import './star-rating.css';
import {StudentHttpClient as StudentInterceptor} from '../Interceptors/StudentInterceptor';
import url from '../../../../globalModal/url';
import {withSnackbar} from 'notistack';
class StarRating extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stars:props.rating.value,
      users:props.rating.users,
      isAlreadyVoted:true,
      blogId:props.blogId
    };
  }
  checkIfUserAlreadyVoted(){
      let checkurl=url.checkIfUserAlreadyVoted+"?blogId="+this.state.blogId;
StudentInterceptor.get(checkurl).then(result=>{
        let data=result.data;
        console.log(data);
    if(data.status=='S'){
        this.setState({
            ...this.state,
          isAlreadyVoted:data.result.isAlreadyVoted
        })

    }
}).catch(err=>{
  // if(err.response!=null){
  //   this.props.enqueueSnackbar(err.response.data.message,{ 
  //     variant: 'error'
  // });
  //  }else{
  //   this.props.enqueueSnackbar(err.message,{ 
  //     variant: 'error'
  // })
  //  }
})
  }

  componentDidMount() {
    this.checkIfUserAlreadyVoted();
  }

  downVote(){
      let downVoteurl=url.downVote+"?blogId="+this.state.blogId;
    StudentInterceptor.get(downVoteurl).then(result=>{
        let data=result.data;
       if(data.status=='S'){
      
         this.setState({
              ...this.state,
              stars:this.state.stars-1,
              users:this.state.users+1,
              isAlreadyVoted:true
         })
       }
    
        }).catch(err=>{
          if(err.response!=null){
            this.props.enqueueSnackbar(err.response.data.message,{ 
              variant: 'error'
          });
           }else{
            this.props.enqueueSnackbar(err.message,{ 
              variant: 'error'
          })
           }
        })
      }

  upVote(){
    let upVoteurl=url.upVote+"?blogId="+this.state.blogId;
    StudentInterceptor.get(upVoteurl).then(result=>{
    let data=result.data;
   if(data.status=='S'){
  
     this.setState({
          ...this.state,
          stars:this.state.stars+1,
          users:this.state.users+1,
          isAlreadyVoted:true
     })
   }

    }).catch(err=>{
      if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
    })
  }

  render() {
    return (
    <div id='starcontainer'>
       
       <div>
      {this.state.isAlreadyVoted==false?
       <div  className="upvotetriangle" onClick={()=>{this.upVote()}}></div>:
       <div  className="disableuvt" ></div>
    }
      
       <div>
            {this.state.stars}
       </div>
       {this.state.isAlreadyVoted==false?
    <div className="downvotetriangle" onClick={()=>{this.downVote()}}></div>:
       <div  className="disabledvt" ></div>
    }

       </div>
     
      
    </div> 
    
    )
  }
}

export default withSnackbar(StarRating);