import React from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import {withSnackbar} from 'notistack';
//import axios from 'axios';
//import url from '../../../globalModal/url';
import './blogContent.css'
class BlogContent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            edit: null,
            initialParaData: null,
            finalParaData: null
        }
    }

    editPara(content) {
        //console.log('reached here')
        this.setState({ edit: content, initialParaData: content.data })
    }

    handleParaChanges=event=>{
        //console.log('rnning')
        this.setState({finalParaData:event.target.value});
    }

    savePara() {
        //save the new content
        if(this.state.finalParaData!=null){
            //call parent function to make change in the data of the content
            //set the state properties to null
            this.props.handleEdit(this.state.edit,this.state.finalParaData);
            this.setState({edit: null,
                initialParaData: null,
                finalParaData: null})
        }
        else{
          
            this.props.enqueueSnackbar('Enter Paragraph Details...',{ 
                variant: 'error'
            })
        }
    }

    cancelParaChanges(){
        this.setState({edit: null,
            initialParaData: null,
            finalParaData: null})
    }

    delete(content) {
        if (window.confirm("Are you sure about delete?")) {
            //delete the element
            this.props.handleDelete(content.priority)
            //console.log('deleted', content)
        }
        else {
            //dont delete
        }
    }

    render() {




        return (
      <div>
         { this.props.content!=null && this.props.content.length!=0? 
             <div  className='rootblog'>

                {this.props.content.map((content,key) => (
                    <div key={key}>
                        <div  >
                            {content.type == 'PR' ?
                                <div  >
                                    {this.state.edit == content ?
                                      
                                    <div className='flexcontainercon'>
                                        <div className="paraEdit">
                                            <TextField
                                                className="textBox"
                                                label="Multiline"
                                                multiline
                                                rows="5"
                                                defaultValue={this.state.initialParaData}
                                                onChange={this.handleParaChanges}
                                                variant="filled"
                                            />
 </div> 
 <div className="btn">
                                          <div>  <Button variant="outlined" color="primary" onClick={this.savePara.bind(this)}>Save</Button></div>
                                           <div> <Button variant="outlined" color="secondary" onClick={this.cancelParaChanges.bind(this)}>
                                              Cancel</Button></div>
</div>
                                       
                                        </div>:
                                        <div>
                                            <div className='para'>
                                                <div className='subheading'>
                                                    {content.subHeading}
                                                </div>
                                                {content.data}
                                                <span className="delIcons">
                                                <span  onClick={() => this.editPara(content)}><EditIcon fontSize="small" /></span>
                                            <span  onClick={() => this.delete(content)}><DeleteIcon fontSize="small" /></span>
                                            </span>
                                            </div>
                                               </div>}

                                </div> : null}

                        </div>
                        <div className="dataholder">
                            {content.type == 'IM' ?
                                <div>
                                    <div className="imgcontainer">

                                        <img src={content.data} />
                                        <div className='subheading'>
                                            {content.subHeading}
                                        </div>
                                    </div>
                                    <span className="delIcons" onClick={() => this.delete(content)}><DeleteIcon fontSize="small" /></span>
                                </div> : null}

                        </div>
                        <div className="dataholder">
                        {content.type == 'VR' ?
                            <div>
                                <div>
                                    <iframe width="80%" height="315" src={content.data} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                                    <div className='subheading'>
                                        {content.subHeading}
                                    </div>
                                </div>
                                <span className="delIcons" onClick={() => this.delete(content)}><DeleteIcon fontSize="small" /></span>
                            </div>
                      
                            : null}
                            </div>
                    </div>
                ))}
            </div>

        
    :null
                                    }
    </div>
        )
}
}

export default withSnackbar(BlogContent);