import React from 'react';
import './blogContent.css'
class BlogContent  extends React.Component{

  

 render(){


     

     return(
            <div className='rootdata'>

                {this.props.content.content.map((content,key)=> (
                    <div key={key}>
                        <div >
                            {content.type === 'PR' ?
                                <div className='para'>
                                    <div className='subheading'>
                                        {content.subHeading}
                                    </div>
                                    {content.data}
                                </div> : null}

                        </div>
                        <div>
                            {content.type === 'IM' ?
                                <div className="imgcontainer">

                                    <img src={content.data} />
                                    <div className='subheading'>
                                        {content.subHeading}
                                    </div>
                                </div> : null}

                        </div>
                        {content.type === 'VR' ?
                            <div>
                                <iframe width="80%" height="315" src={content.data} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                                <div className='subheading'>
                                    {content.subHeading}
                                </div>
                            </div> : null}
                    </div>
                ))}
            </div>

        )
    }
}

export default BlogContent;