import React, { Component } from 'react'
import EditorComponent from './Editor/editor';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import './blog.css';
import url from '../../../../globalModal/url';
import Checkbox from '@material-ui/core/Checkbox';
import defaultImage from './tbdefault.jpg';
import BlogContent from './BlogContent/blogContent';
import { isTSParenthesizedType } from '@babel/types';
import {withSnackbar} from 'notistack';


export class BlogComponent extends Component {
       _isMounted=false;
    constructor (props) {
        super(props);
        //issue solved
        //use the params property to get the blogId and get the required blog info
        console.log(props)
        this.state = {
            //use this to display form
            catId:null,
            topic:null,
            thumbnail:null,
            thgLink:true,
            gthumbnail:null,
            published:null,
            blogId:null,
            taken:null,
            teacher:null,
            //userId:localStorage.getItem('userId'), //or get from the jwt
            content:[],
            categoryList:[],
           

        }
      }

      componentWillUnmount() {
        this._isMounted = false;
      }
    
      componentDidMount(){
          this._isMounted=true;
        axios.get(url.getStudentCategoryList)
        .then(response=>{
            console.log(response);
            //alert(response.data.msg);
            if(this._isMounted){
            this.setState({categoryList:response.data.result});
            }
        })
        .catch(err=>{
            console.log(err)
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
        })

        
        //to check whether we have to edit the article or write it
        if(this.props.match.params&&this.props.match.params.blogId){
            axios.get(`${url.getStudentBlog}?id=${this.props.match.params.blogId}`)
            .then(response=>{
                console.log(response);
                //set the initial values of the topic,catId etc.
                let obj=response.data.data;
                this.setState({
                    topic:obj.blogTopic.topic,catId:obj.blogTopic.catId,taken:obj.taken, teacher:obj.teacher,
                    thgLink:false,
                    blogId:obj.blogTopic.blogId,thumbnail:obj.blogTopic.thumbnail,published:obj.blogTopic.published,
                     content:obj.blogTemplate.content})
            })
            .catch(err=>{
                console.log(err)
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
                //alert('Some error Occured');
            })
        }
      }

    

    handleChange=input=>event=>{
     
        if(input=='thumbnail'){
        var urlParams = new URLSearchParams(event.target.value);
        var arr=urlParams.entries();
        let gtl=null;
        for(let pair of arr) { 
                gtl=(url.tbh+pair[1]);
            break;
          }
          this.setState({
              ...this.state,
              thumbnail:event.target.value,
              gthumbnail:gtl

          })
        //console.log(this.state)
        }
        this.setState({
            ...this.state,
            [input]:event.target.value
        })
    }
    handleCheckbox=input=>event=>{
      console.log(this.state);
      var urlParams = new URLSearchParams(this.state.thumbnail);
      var arr=urlParams.entries();
      let gtl=null;
      for(let pair of arr) { 
              gtl=(url.tbh+pair[1]);

          break;
        }

        this.setState({
            ...this.state,
            gthumbnail:gtl,
            [input]:event.target.checked,
        })
      //console.log(this.state)
      
    }
    //define a changeHandler for the change in editor to be passed as props
    handleEditor(obj){
        console.log('in blog',obj)
        //fill the details to be shown to the user
        obj.priority=this.state.content.length;  //assigning priority
        let content1=this.state.content;
        content1.push(obj);             //pushing into local array and then replacing the state array
        this.setState({content:content1});
        console.log('in blog',this.state)

    }

    //define a EditHandler which will handle the para edits made in the blogContent
    handleEdit(contentObj,data){
        //console.log(this.state)
        this.state.content.map(obj=>{
            if(obj.priority==contentObj.priority && obj.subHeading==contentObj.subHeading){
                obj.data=data;
            }
        })
        //console.log(this.state)
    }

    //define a deleteHandler which will handle the deletes made in the blogContent
    handleDelete(priority){
        //console.log(this.state)
        let content1=[];
        this.state.content.map(obj=>{
            if(obj.priority>priority){
                obj.priority=obj.priority-1;
                content1.push(obj);
            }
            else if(obj.priority==priority){

            }
            else{
                content1.push(obj);
            }
        })
        this.setState({content:content1});
        //console.log(this.state)
    }

    validate(){
        if(this.state.catId && this.state.thumbnail && this.state.topic && 
            this.state.content.length>0){
            return true;
        }
        else{
            return false;
        }
    }

    submit(){
        //send state to backend
        if(this.state.thgLink==true){
            this.state.thumbnail=this.state.gthumbnail;
        }
        console.log(this.state);
        if(this.validate()){
            axios.post(url.saveBlog,this.state)
            .then(response=>{
                console.log(response);
              //  alert('Successfully Done!!');
                window.location.reload();
            })
            .catch(err=>{
                console.log(err)
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
            })
        }
        else{
           
                this.props.enqueueSnackbar('Enter All Details..',{ 
                  variant: 'error'
              })
               }
      
    }

    render() {
        return (
            <div>
            {this.state.blogId!=null?
              <div className="blog_div">
                    <div className="heading">
                     {this.state.topic}
                   </div>
              {(this.state.published==false && (this.state.taken==null || this.state.taken!=true))?<div>
             

   <div>
               <TextField
                       className="topic"
                       multiline
                       
                       rowsMax="5"
                       value={this.state.topic}
                       onChange={this.handleChange('topic')}
                       margin="normal"
                   />
                   </div>
                   <div className="blgflex">
               
                      <div className="blgimgc" >
                     {this.state.thgLink==true?
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.gthumbnail||defaultImage} />:
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.thumbnail||defaultImage} />
                     
                     }
                      
                       </div>
                      

                   <div className="thb">
                 <TextField
                       className="topic"
                       multiline
                       label="Thumbnail"
                       rowsMax="5"
                       value={this.state.thumbnail}
                       onChange={this.handleChange('thumbnail')}
                       margin="normal"
                   />
                      <div> 
                Is it a Google Drive Link ? <Checkbox
                  checked={this.state.thgLink}
                     onChange={this.handleCheckbox('thgLink')}
                      inputProps={{ 'aria-label': 'Google Link ?' }}
                   />

           </div>
                   </div>
                   </div>
              </div>
              :<div>
                  <div className="binf">

Already Assigned To A Teacher 
</div>
                   <TextField
                       label="Topic"
                       className="topic"
                       multiline
                       rowsMax="5"
                       value={this.state.topic}
                       margin="normal"
                   />
                    <div className="blgflex">
                 
                    <div className="blgimgc" >
                    {this.state.thgLink==true?
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.gthumbnail||defaultImage} />:
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.thumbnail||defaultImage} />
                     
                     }
                       </div>
                       

                   <div className="thb">
                 <TextField
                       className="topic"
                       multiline
                       label="Thumbnail"
                       rowsMax="5"
                       value={this.state.thumbnail}
                       margin="normal"
                   />
                     <div> 
                Is it a Google Drive Link ? <Checkbox
                  checked={this.state.thgLink}
                     onChange={this.handleCheckbox('thgLink')}
                      inputProps={{ 'aria-label': 'Google Link ?' }}
                   />

           </div>
                   </div>
                   </div>
                   <FormControl className="dropdown">
                       <InputLabel>Category</InputLabel>
                       <Select
                           value={this.state.catId||''}
                           labelId="demo-simple-select-label"
                           onChange={this.handleChange('catId')}
                       >
                       {this.state.categoryList.map((cat,i)=>(
                           <MenuItem value={cat.catId} key={i}>{cat.catName}</MenuItem>
                       ))}
                           
                       </Select>
                   </FormControl>
               </div>}
               
               <BlogContent content={this.state.content} handleDelete={this.handleDelete.bind(this)}
                handleEdit={this.handleEdit.bind(this)}/>
               <div>
                  { (this.state.published==false  && (this.state.taken==null || this.state.taken==false))? <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                       style={{ margin: '20px' }}>
                       Submit
                   </Button>:<span></span>
                   }
               </div>
               <EditorComponent handleEditor={this.handleEditor.bind(this)}/>
               
           </div>:
           <div className="blog_div">
                  <div className="heading">
                    Write New Article
                   </div>
                    <TextField
                        label="Topic"
                        className="topic"
                        multiline
                        rowsMax="5"
                        onChange={this.handleChange('topic')}
                        margin="normal"
                    />
                   <div className="blgflex">
                
                   <div className="blgimgc" >
                   {this.state.thgLink==true?
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.gthumbnail||defaultImage} />:
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.thumbnail||defaultImage} />
                     
                     }
                       </div>

                   <div className="thb">
                 <TextField
                       className="topic"
                       multiline
                       label="Thumbnail"
                       rowsMax="5"
                     
                       onChange={this.handleChange('thumbnail')}
                       margin="normal"
                   />
                     <div> 
                Is it a Google Drive Link ? <Checkbox
                  checked={this.state.thgLink}
                     onChange={this.handleCheckbox('thgLink')}
                      inputProps={{ 'aria-label': 'Google Link ?' }}
                   />

           </div>
                   </div>
                   </div>
                    <FormControl className="dropdown">
                        <InputLabel>Category</InputLabel>
                        <Select
                            value={this.state.catId||''}
                            labelId="demo-simple-select-label"
                            onChange={this.handleChange('catId')}
                        >
                        {this.state.categoryList.map((cat,i)=>(
                            <MenuItem value={cat.catId} key={i}>{cat.catName}</MenuItem>
                        ))}
                            
                        </Select>
                    </FormControl>
                    <BlogContent content={this.state.content} handleDelete={this.handleDelete.bind(this)}
                 handleEdit={this.handleEdit.bind(this)}/>
                <div>
                   <Button color="primary" variant="contained" onClick={this.submit.bind(this)}
                        style={{ margin: '20px' }}>
                        Submit
                    </Button>
                  
                </div>
                <EditorComponent handleEditor={this.handleEditor.bind(this)}/>
                
                </div>}
                
              
            </div>
        


        )
    }
}

export default withSnackbar(BlogComponent);
