import React from 'react';
import axios from 'axios';
import url from '../../../../globalModal/url';
import TextField from '@material-ui/core/TextField';
import BlogDisp from '../../sharedModule/blogDisplay';
import HomeHeader from '../../HomeModule/home/homeHeader';
export default class BlogSearchPage extends React.Component{

state={
 blogs:[]
}

searchWithText(text){
 let searchurl=url.searchBlogUrl+"?key="+text;
 axios.get(searchurl).then((data)=>{
     if(data.data.status==='S'){
         let sorted= data.data.blogs.sort((a,b)=>{
             return b.stars.value-a.stars.value
         })
         
     this.setState({
         blogs:sorted
     })
     }
 }).catch(err=>{
     if(err.response){
         alert(err.response.data.message);
     }
 })

}
handleChange=input=>event=>{
   if(event.target.value!=null && event.target.value.length>=4){
    this.searchWithText(event.target.value)
   }
    
}


render(){

    const style={
        root:{
            backgroundColor:'white',
            paddingTop:'7rem',
            width:"84.5%",
            margin:'auto'
        },
        inputhere:{
            width:'80%',
            margin:'2rem'
        },
        heading:{
            fontSize:'40px',
          textTransform:'uppercase',
          textAlign:'left',
          marginLeft:'2rem'
             },
             blogData:{
                 margin:'2rem'
             }
    }
    return (
        <div>
            <div style={{width:'84.5%',margin:'auto'}}>
            <HomeHeader/>
            </div>
        <div style={style.root}>
 
            <div>
            <TextField style={style.inputhere} id="outlined-search" label="Search field" type="search" variant="outlined" onChange={this.handleChange()}/>
            </div>
            <div style={style.heading}>
                Search Result({this.state.blogs.length})
            </div>
            <div style={style.blogData}>
            <BlogDisp blog={this.state.blogs}/>
            </div>

        </div>
        </div>
    )
}

}