import React from 'react';
import axios from 'axios';
import url from '../../../../globalModal/url';
import VerticalTabs from './dataTabs';
import {withSnackbar} from 'notistack';
 class EmpVerification extends React.Component{
constructor(){
super();
this.state={
  unverified:[],
  verified:[]
}
}



getUnverifiedTeacher(){
  axios.get(url.getunverifiedTeacher).then(data=>{
  //  console.log(data)
    if(data.data.status==='S'){
    
 this.setState({
   unverified:data.data.teacher,
   verified:this.state.verified
 })
    }
  }).catch(err=>{
   
   if(err.response!=null){
    this.props.enqueueSnackbar(err.response.data.message,{ 
      variant: 'error'
  });
   }else{
    this.props.enqueueSnackbar(err.message,{ 
      variant: 'error'
  });
   }
  
  })
}

getverifiedTeacher(){
  axios.get(url.getverifiedTeacher).then(data=>{
  //  console.log(data)
    if(data.data.status==='S'){
    
 this.setState({
   unverified:this.state.unverified,
   verified:data.data.teacher
 })
    }
  }).catch(err=>{
  
  
   
    if(err.response!=null){
      this.props.enqueueSnackbar(err.response.data.message,{ 
        variant: 'error'
    });
     }else{
      this.props.enqueueSnackbar(err.message,{ 
        variant: 'error'
    });
     }
  })
}

componentDidMount(){
this.getUnverifiedTeacher();
this.getverifiedTeacher();
}

rejectUser(id){
  axios.post(url.rejectTeacher,{id:id}).then(data=>{
    //  console.log(data)
      if(data.data.status==='S'){
      
   this.getUnverifiedTeacher();
   this.getverifiedTeacher();
      }
    }).catch(err=>{
    
      
       
   if(err.response!=null){
    this.props.enqueueSnackbar(err.response.data.message,{ 
      variant: 'error'
  });
   }else{
    this.props.enqueueSnackbar(err.message,{ 
      variant: 'error'
  });
   }
    })
}

verifyUser(id){
  axios.post(url.verifyTeacher,{id:id}).then(data=>{
    //  console.log(data)
      if(data.data.status==='S'){
      
   this.getUnverifiedTeacher();
   this.getverifiedTeacher();
      }
    }).catch(err=>{
    
     
      
   if(err.response!=null){
    this.props.enqueueSnackbar(err.response.data.message,{ 
      variant: 'error'
  });
   }else{
    this.props.enqueueSnackbar(err.message,{ 
      variant: 'error'
  });
   }
    })
}

verify=(id)=>{
this.verifyUser(id);
}


reject=(id)=>{
this.rejectUser(id);
}

render(){

    return(
  <div>
      <VerticalTabs unvlist={{unverified:this.state.unverified}}  vlist={{verified:this.state.verified}} verify={this.verify} reject={this.reject} />
  </div>
  

    )
}

}
export default withSnackbar(EmpVerification);