import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import url from '../../../globalModal/url';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  bigAvatar: {
    width: 60,
    height: 60,
  },
  rollno:{
      fontSize:'13px'
  },
  prof:{
   fontSize:'14px',
   color:'gray',
   fontWeight:'600'
  },
  email:{
      paddingTop:'2px',
      fontSize:'13px'
  },
  datacontainer:{
    textAlign: 'left',
    marginTop: '3px',
    padding: '10px'
  },
  name:{
      fontSize:'15px'
  }
}));

export default function UserProfile(props) {
  const classes = useStyles();
  if(props.user.dpLink!=null){
    var urlParams = new URLSearchParams(props.user.dpLink);
    var arr=urlParams.entries();
    let gtl=null;
    for(let pair of arr) { 
            gtl=(url.tbh+pair[1]);
  
        break;
      }
      props.user.dpLink=gtl;
  }



 console.log(props);

  return (
      
    <div className={classes.root}>
    <div>
      {props.user.dpLink!=null?
       <Avatar alt="user" src={props.user.dpLink} className={classes.bigAvatar} />:
       <Avatar alt="user" src="https://www.pngfind.com/pngs/m/292-2924858_user-icon-business-man-flat-png-transparent-png.png" className={classes.bigAvatar} />

    }
           </div>
      <div className={classes.datacontainer}>
      <div>
         {props.user.rollno!=null? <span className={classes.rollno}>
         <span className={classes.name}>{props.user.name}</span>      DTU:{props.user.rollno}
      </span>: 
      <div>

      <span className={classes.prof}>Teacher</span>   <span className={classes.name}>{props.user.name}</span>
     </div>
      }
      </div>
      <div className={classes.email}>
          {props.user.email}
      </div>
      
    </div>
    </div>
  );
}