import React from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import url from '../../../../globalModal/url';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Category from './category';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router';
import { withSnackbar } from 'notistack';


class Categories extends React.Component{

    constructor(){
      super();
      this.state={
        categories:[]
      }
    }
    static propTypes = {
      match: PropTypes.object.isRequired,
      location: PropTypes.object.isRequired,
      history: PropTypes.object.isRequired
    }


    getCategories(){
      axios.get(url.getAllCategory).then(data=>{
      //  console.log(data)
        if(data.data.status==='S'){
        //  console.log(data.data.result);
     this.setState({
       categories:data.data.result
     })
        }
      }).catch(err=>{
        if(err.response!=null){
          this.props.enqueueSnackbar(err.response.data.message,{ 
            variant: 'error'
        });
         }else{
          this.props.enqueueSnackbar(err.message,{ 
            variant: 'error'
        })
         }
      })
    }
  
  componentDidMount(){
    this.setState(this.state);
    this.getCategories();
  }
  

  createNew=()=>{
    this.state.categories.push(
      {
        catId:'',
        catName:'',
        active:false
      }
    )
this.setState({
categories:this.state.categories
})
  }
    
render(){
  const style={
  root: {
    width: '100%',
  },
  heading: {
    fontSize: '20px',
    fontWeight: 600,
  }
}
const { match, location, history } = this.props;

    return (
  <div>
      { this.state.categories.map(category=>(
      <div key={category.catId} style={style.root}>
       <ExpansionPanel style={{opacity:'0.9'}}>
       <ExpansionPanelSummary
         expandIcon={<ExpandMoreIcon />}
         aria-controls="panel1a-content"
         id="panel1a-header"
       >
      <span style={style.heading}>{category.catName}</span>
       </ExpansionPanelSummary>
       <ExpansionPanelDetails>
         <div style={style.root}>
         <Category eachcategory={category}></Category>
   
         </div>
       </ExpansionPanelDetails>
     </ExpansionPanel>
</div>
      ))
      }
      <div>
        <Button color="primary" variant="contained" style={{marginTop:'1rem'}}onClick={this.createNew}>Create New</Button>
      </div>
  </div>

    )
}



}


export default withSnackbar(Categories)