import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
// import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
// import InboxIcon from '@material-ui/icons/MoveToInbox';
// import MailIcon from '@material-ui/icons/Mail';
import {Link} from 'react-router-dom';
import { deepPurple } from '@material-ui/core/colors';
const useStyles = makeStyles({
  list: {
    width: '100%',
  },
  fullList: {
    width: 'auto',
  },
  head:{
      backgroundColor:'#833471',
      padding:"8%",
      textTransform:'uppercase',
      textAlign:'center',
      fontSize:'24px',
      fontWeight:600,
      color:'white'
  }
});

let storeIt=(category)=>{
    
        localStorage.setItem('category',category);
    
}

export default function MenuDrawerNav(props) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
       <div className={classes.head}>
          CATEGORIES
       </div>
      
      <List>
        {props.categories.map((value, index) => (
          <ListItem button key={index}>
          
            <ListItemText  >
            <Link to={`/category/${value.catId}`} className="linkheader"><Button onClick={()=>storeIt(value.catName)} className="buttonheader">{value.catName}</Button></Link>
  
            </ListItemText>
            <Divider />
          </ListItem>
          
          
        ))}
      </List>
     
     
    </div>
  );

  return (
    <div>
     
        <React.Fragment >
          <Button color="primary" variant="outlined" onClick={toggleDrawer('left', true)} >Menu</Button>
          <SwipeableDrawer
            anchor={'left'}
            open={state.left}
            onClose={toggleDrawer('left', false)}
            onOpen={toggleDrawer('left', true)}
          >
            {list('left')}
          </SwipeableDrawer>
        </React.Fragment>
  
    </div>
  );
}