import React, { Component } from 'react'
import Header from '../Header/header'
import BlogComponent  from '../Blog/blog';
import listBlogs from '../newBlogs/listBlogs';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import MyBlogs from '../myblogs/myblogs';
import TeacherInfoComponent from '../PersonalInfoComponent/personalInfo';
import { TeacherHttpClient } from '../../sharedModule/Interceptors/TeacherInterceptor';
import './home.css';
import Sidenav from '../sidenav/sidenav';
import {withSnackbar, SnackbarProvider} from 'notistack';
import WriteBlogComponent from '../Blog/writeNewBlog';
import url from '../../../../globalModal/url';

const classes={
  
  }

export class Home extends Component {

    constructor(props) {
        super(props)
        //console.log(this.props)
        this.state = {
            jwtVerified: false    //param protecting unauthorized access to any teacher route
        }
    }


    componentWillMount() {
        TeacherHttpClient.get(url.checkTeacherLogin).then(response => {
            if (response.data.message) {
                console.log(response.data.message);
                this.setState({ jwtVerified: true })
            }
        }).catch(err => {
            console.log(this.props)
            localStorage.removeItem('teacherToken');
            this.props.history.push('/teacherLogin')
        })
    }


    render() {

 

        if (this.state.jwtVerified) {
            return (
                <div>
                    <Header />
                 <div className="flexcontainer">
                     <div className='teachersidenav'>                  <Sidenav/>
</div>
                  <main className="teachercontent">
                      
                    <Switch>
                        <Route path="/teacher/newblogs" component={listBlogs}/>
                       <Route path="/teacher/myblogs" component={MyBlogs}/>
                       <Route path="/teacher/myaccount" component={TeacherInfoComponent}/>
                        <Route path="/teacher/edit/:blogId" component={BlogComponent}/> 
                        <Route path="/teacher/newBlog" component={WriteBlogComponent} />
                    </Switch>
                    </main>
                </div>
                </div>
            )
        }
        else {
            return null
        }
    }
}

export default withSnackbar(Home);
