import React from 'react';
import {Router,Route,Switch} from 'react-router';
import {createBrowserHistory} from 'history';
import Blog from './Blog';
import SignUpComponent from './modules/sharedModule/signUpComponent/signup';
import LoginComponent from './modules/sharedModule/LoginComponent/loginComponent';
import adminLogin from './modules/adminModule/adminlogin';
import adminhomepage from './modules/adminModule/adminhomepage';
//import Categories  from './modules/adminModule/categoryCrud/categories';
import BlogPage from './modules/BlogModule/blogPage';
import BlogComponent from './modules/sharedModule/Blog/blog';
import CategoryBlog from './modules/sharedModule/CategoryBlog/categoryBlog';
import BlogSearchPage from './modules/BlogModule/searchPages/searchpage';
import StudentHome from './modules/studentModule/Home/home';
import TeacherHome from './modules/teacherModule/Home/home';
import TeacherSignUp from './modules/teacherModule/signUp/signupComp'
import TeacherLogin from './modules/teacherModule/LoginComp/loginComponent'
import ErrorComponent from './modules/sharedModule/ErrorPage';
import { SnackbarProvider } from 'notistack';
import passwordChange from './modules/sharedModule/passwordOptions/passwordChange';

 class BlogRouter extends React.Component{
    render(){
        const history= createBrowserHistory();
        return(
            
            <Router history={history}>
                <Switch>
            <Route exact path="/" component={Blog}></Route>
            <Route exact path="/signUp" component={SignUpComponent}/>
            <Route exact path="/login" component={LoginComponent}/>
            <Route exact path='/category/:id' component={CategoryBlog}/>
            <Route exact path='/search' component={BlogSearchPage} />
            <Route exact path='/student/home' component={BlogComponent}/>
            <Route exact path="/adminlogin" component={adminLogin}/>
            <Route exact path="/blog/:id" component={BlogPage}/>
            <Route  path="/student" component={StudentHome}/>
            <Route path="/teacherSignup" component={TeacherSignUp}/>
        <Route path="/teacherLogin" component={TeacherLogin}/>
    <Route path="/passwordcreate" component={passwordChange}/>
        <Route path="/teacher" component={TeacherHome}/>
    
           <Route path='/admin' component={adminhomepage}/>
           <Route path="" component={ErrorComponent}/>
              
        
           </Switch>
            
        </Router>
        )
    }
}


export default BlogRouter;