import React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import axios from 'axios';
import url from '../../../../globalModal/url';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router'
import {AdminHttpClient} from '../../sharedModule/Interceptors/AdminInterceptor';
import {  withSnackbar } from 'notistack';
 class Category extends React.Component{
    constructor(props) {
      
        super(props);
        this.state = {
            catId: '',
            catName: '',
            active: false,
            
        }

    }



 save=()=>{
     console.log(this.state);
  AdminHttpClient.post(url.saveCategoryUrl,this.state).then((data)=>{
    //  console.log(data)
    if(data.data.status==='S'){
      this.props.enqueueSnackbar('Successfully Updated',{ 
        variant: 'success'
    });
window.location.reload();
    }
  }).catch(err=>{
      console.log(err);
      if(err.response!=null){
        this.props.enqueueSnackbar(err.response.data.message,{ 
          variant: 'error'
      });
       }else{
        this.props.enqueueSnackbar(err.message,{ 
          variant: 'error'
      })
       }
  })
}



 delete =()=>{
    AdminHttpClient.post(url.deleteCategoryUrl,this.state).then((data)=>{
        //  console.log(data)
        if(data.data.status==='S'){
   // this.props.history.push('/admin')
   this.props.enqueueSnackbar('Successfully Deleted',{ 
    variant: 'success'
});
   window.location.reload();
        }
      }).catch(err=>{

        if(err.response!=null){
          this.props.enqueueSnackbar(err.response.data.message,{ 
            variant: 'error'
        });
         }else{
          this.props.enqueueSnackbar(err.message,{ 
            variant: 'error'
        })
         }
      })
 }

 
    componentDidMount(){
      //  console.log(this.props);
     
       this.setState({
         ...this.state,
        catId:this.props.eachcategory.catId,
        catName:this.props.eachcategory.catName,
        active:this.props.eachcategory.active
    });
       
   }
     
   handleChange=input=>event=>{
    this.setState({[input]:event.target.value});
}

setActiveStatus=input=>event=>{
    this.setState({[input]:event.target.checked});
}

   render(){

    const style={
        root:{
            width:'100%'
        }
        ,
        flexcontainer:{
            display:'flex',
            justifyContent:'space-between'
        }
    }
       return (
           <div style={style.root}>
               <div style={style.flexcontainer}>
            <div>
  
              <TextField
                 placeholder="Enter Category Name "
                 label="Category Name"
                 value={this.state.catName}
                 onChange={this.handleChange('catName')}
                />
   </div>
<div>
   <FormGroup row>
      <FormControlLabel
        control={
          <Checkbox checked={this.state.active} onChange={this.setActiveStatus('active')} value="active" />
        }
        label="Active"
      />
      </FormGroup>
      </div>
</div>
<div>
    <Button onClick={this.save} >Save</Button>
    <Button onClick={this.delete}> Delete</Button>
</div>

           </div>

       )
   }
}
export default withSnackbar(Category)