import React, { Component } from 'react'
import axios from 'axios';
import url from '../../../../globalModal/url';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import defaultImage from './default_user.png'
import './personalInfo.css'
import SnackBarAlert from '../../sharedModule/SnackBarAlert/snackBarAlert';

export class TeacherInfoComponent extends Component {
    
    tbh="https://drive.google.com/thumbnail?id=";
    constructor(props) {
        super(props);
        this.state = {
            data: null,  //recieve initial data 
            dp:null,
            formData: {
                dpLink:null,
                documentLink:null,
            },          //final form data
            openErrorSnack: false,
            openSuccessSnack: false
        }
    }

    componentWillMount() {
        axios.get(`http://${url.base.url}/${this.props.match.path.split('/')[1]}/getPersonalInfo`)
            .then(response => {
                if (response.data) {
                    console.log(response)
                    this.setState({ data: response.data.result })
                    //fill some of the final data form details

                    var urlParams = new URLSearchParams(response.data.result.dpLink);
                    var arr=urlParams.entries();
                    for(let pair of arr) { 
                        this.state.dp=this.tbh+pair[1];
                        break;
                      }
                             
                    this.setState({
                        dp:this.state.dp,
                        formData:{
                        dpLink:response.data.result.dpLink,
                        documentLink:response.data.result.documentLink
                    }})
                }
            })
            .catch(err => {
                console.log(err);
                this.setState({ openErrorSnack: true })
            })
    }

   

    update() {
        console.log(this.state.formData)
       
            axios.post(`http://${url.base.url}/${this.props.match.path.split('/')[1]}/updateProfile`
                ,this.state.formData)
             .then(response=>{
                 if(response.data){
                     //console.log(response.data.result)
                     if(response.data.result.isUpdated){
                         this.setState({openSuccessSnack:true})
                     }
                 }
             })
             .catch(err=>{
                 this.setState({openErrorSnack:true})
             })
        
    }

 
    handleDPLink=e=>(event)=>{
        this.setState({
            ...this.state,
            formData:{
            dpLink:event.target.value,
            documentLink:this.state.formData.documentLink
        }})
    }
    handledocument=e=>(event)=>{
        this.setState({
            ...this.state,
            formData:{
            documentLink:event.target.value,
            dpLink:this.state.formData.dpLink
           
        }})
    }

    //show and edit the following info
    //1.Profile pic
    //2.Username (rollno and email shouldnt be edited)
    render() {
        return (
            <div className="main">
                {this.state.data ? <div>
                    <div className="bannerhead">
                        {this.state.data.name}
                        </div>
                    <div className="imageDiv"> <img alt="Profile pic"  //watchout for infinite callback if the onerror image goes missing
                        onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                        src={this.state.dp||defaultImage}/></div>
                    <div>
                        <TextField
                            className="txtarea"
                            label="Profile Picture Link"
                            multiline
                            rowsMax="2"
                            defaultValue={this.state.data.dpLink}
                            onChange={this.handleDPLink('dpLink')}
                            margin="normal"
                        />
                        <TextField
                            className="txtarea"
                            label="Username"
                            value={this.state.data.name}
                            multiline
                            rowsMax="1"
                            margin="normal"
                        />
                        <TextField
                            className="txtarea"
                            label="Email"
                            value={this.state.data.email}
                            multiline
                            rowsMax="2s"
                            margin="normal"
                        />
                           <TextField
                            className="txtarea"
                            label="Degree "
                            defaultValue={this.state.data.documentLink}
                            multiline
                            rowsMax="2"
                            onChange={this.handledocument('documentLink')}
                            margin="normal"
                        />
                           <TextField
                            className="txtarea"
                            label="Phone No "
                            value={this.state.data.phoneno}
                            multiline
                            rowsMax="1"
                            margin="normal"
                        />
                        
                    </div>
                    <Button color="primary" variant="contained" onClick={this.update.bind(this)}
                        style={{ margin: '20px' }}>
                        Update Profile
                    </Button>
                </div> : null}

                {this.state.openErrorSnack ? <SnackBarAlert message="Some Error Occured" severity="error" /> : null}
                {this.state.openSuccessSnack ? <SnackBarAlert message="Profile Updated" severity="success" /> : null}

            </div>
        )
    }
}

export default TeacherInfoComponent
