import React, { Component } from 'react'
import Header from '../Header/header'
import  BlogComponent  from '../../sharedModule/Blog/blog';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import  MyBlogsComponent from "../../sharedModule/MyBlogsComponent/myblogs";
import { StudentHttpClient } from '../../sharedModule/Interceptors/StudentInterceptor';
import PersonalInfoComponent from '../../sharedModule/PersonalInfoComponent/personalInfo'
import Sidenav from '../sidenav/sidenav';
import './home.css';
import {withSnackbar} from 'notistack';
import url from '../../../../globalModal/url';
export class Home extends Component {

    constructor(props) {
        super(props)
        //console.log(this.props)
        this.state = {
            jwtVerified: false
        }
    }


    componentWillMount() {
        StudentHttpClient.get(url.checkLogin).then(response => {
            if (response.data.message) {
                console.log(response.data.message);
                this.setState({ jwtVerified: true })
            }
        }).catch(err => {
            console.log(this.props)
            localStorage.setItem('studentToken',null);
            this.props.history.push('/login')
        })
    }


    render() {

        if (this.state.jwtVerified) {
            return (
                <div>
                    <Header />
                    <div className="flexcontainer">
                  <Sidenav/>

                  <main className="content">
                    <Switch>
                        <Route path="/student/new" exact component={BlogComponent}/>
                        <Route path="/student/myblogs" component={MyBlogsComponent} />
                        <Route path="/student/edit/:blogId" component={BlogComponent}/> 
                        <Route path="/student/myaccount" component={PersonalInfoComponent} />
                    </Switch>
                    </main>
                    </div>
                </div>
            )
        }
        else {
            return null
        }
    }
}

export default withSnackbar(Home);
