import React, { Component } from 'react'
import EditorComponent from './Editor/editor';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import { TeacherHttpClient } from '../../sharedModule/Interceptors/TeacherInterceptor';
import './blog.css';
import Checkbox from '@material-ui/core/Checkbox';
import CircularProgress from '@material-ui/core/CircularProgress';
import defaultImage from './tbdefault.jpg';
import BlogContent from './BlogContent/blogContent';
import { isTSParenthesizedType } from '@babel/types';
import url from '../../../../globalModal/url';
import {withSnackbar} from 'notistack';

class BlogComponent extends Component {
   
    constructor (props) {
        super(props);
        //issue solved
        //use the params property to get the blogId and get the required blog info
        console.log(props)
        this.state = {
            //use this to display form
            catId:null,
            topic:null,
            thumbnail:null,
            thgLink:true,
            gthumbnail:null,
            published:null,
            blogId:null,
            taken:null,
            teacher:null,
            //userId:localStorage.getItem('userId'), //or get from the jwt
            content:[],
            categoryList:[],
             allowEdit:false,
           

        }
      }

      checkAllowEdit(){
        if(this.state.taken==null || this.state.taken==false ){
             this.state.allowEdit=false;
            this.setState(this.state);
          } else {
              this.checkIfTeacherisMe();

          }  
      }


       checkIfTeacherisMe(){
                TeacherHttpClient.get(url.getPersonalInfo).then(response=>{
                    console.log(response);
                    //alert(response.data.msg);
                    if(response.data.status=='S'){
                  if(this.state.teacher.teacherId==response.data.result.id){
                      this.setState({
                          ...this.state,
                          allowEdit:true
                      })
                  }else{
                      this.setState({
                          ...this.state,
                          allowEdit:false
                      })
                  }


                    }
                })
                .catch(err=>{
                    this.setState({
                        ...this.state,
                        allowEdit:false
                    })
                    if(err.response!=null){
                        this.props.enqueueSnackbar(err.response.data.message,{ 
                          variant: 'error'
                      });
                       }else{
                        this.props.enqueueSnackbar(err.message,{ 
                            variant: 'error'
                        })
                       }
                    // alert('Some error Occured');
                })
          

       }

      componentDidMount(){
        TeacherHttpClient.get(url.getTeacherCategoryList).then(response=>{
            console.log(response);
            
            //alert(response.data.msg);
            this.setState({
                ...this.state,
                categoryList:response.data.result})
        })
        .catch(err=>{
            console.log(err)
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
        })
this.getData();
        //to check whether we have to edit the article or write it
      
      }
    
      getData(){
        if(this.props.match.params&&this.props.match.params.blogId){
            TeacherHttpClient.get(`${url.getTeacherBlog}?id=${this.props.match.params.blogId}`)
            .then(response=>{
                
             
                console.log(response);
                //set the initial values of the topic,catId etc.
                let obj=response.data.data;
                this.setState({
                    ...this.state,
                    topic:obj.blogTopic.topic,
                    catId:obj.blogTopic.catId,
                    taken:obj.taken,
                    teacher:obj.teacher,
                    thgLink:false,
                    blogId:obj.blogTopic.blogId,thumbnail:obj.blogTopic.thumbnail,published:obj.blogTopic.published,
                     content:obj.blogTemplate.content})
                     this.checkAllowEdit();
            })
            .catch(err=>{
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
                //alert('Some error Occured');
            })
        }
      }
    

    // handleChange=input=>event=>{
    //     this.setState({
    //         ...this.state,
    //         [input]:event.target.value});
    //     //console.log(this.state)
        
    // }

    //define a changeHandler for the change in editor to be passed as props
    handleEditor(obj){
        console.log('in blog',obj)
        //fill the details to be shown to the user
        obj.priority=this.state.content.length;  //assigning priority
        let content1=this.state.content;
        content1.push(obj);             //pushing into local array and then replacing the state array
        this.setState({
            ...this.state,
            content:content1});
        console.log('in blog',this.state)

    }

    //define a EditHandler which will handle the para edits made in the blogContent
    handleEdit(contentObj,data){
        //console.log(this.state)
        this.state.content.map(obj=>{
            if(obj.priority==contentObj.priority && obj.subHeading==contentObj.subHeading){
                obj.data=data;
            }
        })
        //console.log(this.state)
    }

    //define a deleteHandler which will handle the deletes made in the blogContent
    handleDelete(priority){
        //console.log(this.state)
        let content1=[];
        this.state.content.map(obj=>{
            if(obj.priority>priority){
                obj.priority=obj.priority-1;
                content1.push(obj);
            }
            else if(obj.priority==priority){

            }
            else{
                content1.push(obj);
            }
        })
        this.setState({
            ...this.state,
            content:content1});
        //console.log(this.state)
    }

    validate(){
        if(this.state.catId && this.state.thumbnail && this.state.topic && 
            this.state.content.length>0){
            return true;
        }
        else{
            return false;
        }
    }

    changeStatus(){
                 TeacherHttpClient.post(url.allowMeToEdit,this.state).then(response=>{
                   // console.log(response);
                   if(response.data.status=='S'){
                       this.getData();
                   }
                  
                })
                .catch(err=>{
                    console.log(err)
                    if(err.response!=null){
                        this.props.enqueueSnackbar(err.response.data.message,{ 
                          variant: 'error'
                      });
                       }else{
                        this.props.enqueueSnackbar(err.message,{ 
                          variant: 'error'
                      })
                       }
                })
           
            
        }

        handleChange=input=>event=>{
     
            if(input=='thumbnail'){
            var urlParams = new URLSearchParams(event.target.value);
            var arr=urlParams.entries();
            let gtl=null;
            for(let pair of arr) { 
                    gtl=(url.tbh+pair[1]);
                break;
              }
              this.setState({
                  ...this.state,
                  thumbnail:event.target.value,
                  gthumbnail:gtl,

    
              })
            //console.log(this.state)
            }
            this.setState({
                ...this.state,
                [input]:event.target.value
            })
        }
        handleCheckbox=input=>event=>{
            console.log(this.state);
            var urlParams = new URLSearchParams(this.state.thumbnail);
            var arr=urlParams.entries();
            let gtl=null;
            for(let pair of arr) { 
                    gtl=(url.tbh+pair[1]);
      
                break;
              }
      
              this.setState({
                  ...this.state,
                  gthumbnail:gtl,
                  [input]:event.target.checked,
              })
            //console.log(this.state)
            
          }

  publish(){
    if(this.state.thgLink==true){
        this.state.thumbnail=this.state.gthumbnail;
        }
    TeacherHttpClient.post(url.publishBlog,this.state)
    .then(response=>{
        console.log(response);
       this.getData();
    })
    .catch(err=>{
     
        
            if(err.response!=null){
                this.props.enqueueSnackbar(err.response.data.message,{ 
                  variant: 'error'
              });
               }else{
                this.props.enqueueSnackbar(err.message,{ 
                  variant: 'error'
              })
               }
        
    })

  }

    submit(){
        //send state to backend
        if(this.state.thgLink==true){
        this.state.thumbnail=this.state.gthumbnail;
        }
        console.log(this.state);
        if(this.validate()){
            TeacherHttpClient.post(url.teacherSaveBlog,this.state)
            .then(response=>{
                console.log(response);
             
                    this.props.enqueueSnackbar('Successfully Submitted',{ 
                      variant: 'success'
                  })
                 
            })
            .catch(err=>{
                console.log(err)
                if(err.response!=null){
                    this.props.enqueueSnackbar(err.response.data.message,{ 
                      variant: 'error'
                  });
                   }else{
                    this.props.enqueueSnackbar(err.message,{ 
                      variant: 'error'
                  })
                   }
            })
        }
        else{
            
                this.props.enqueueSnackbar('Enter All Details..',{ 
                  variant: 'error'
              })
          
        }
        
    }

    render() {
const newcll={
    flc:{
        display:"flex",
        justifyContent:"center"
    },

    bl:{
        marginTop:"10%",
       
        padding:"100px",
        width:"70%",
       
    }
}

        return (
            <div>
            {this.state.blogId!=null?
              <div className="blog_div">
                   <div className="heading">
                     {this.state.topic}
                   </div>
           
                     

              {this.state.allowEdit==true?<div>

               <TextField
                       className="topic"
                       multiline
                       
                       rowsMax="5"
                       value={this.state.topic}
                       onChange={this.handleChange('topic')}
                       margin="normal"
                   />
                     <div className="blgflex">
                
                <div className="blgimgc" >
                {this.state.thgLink==true?
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.gthumbnail||defaultImage} />:
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.thumbnail||defaultImage} />
                     
                     }
                    </div>

                <div className="thb">
              <TextField
                    className="topic"
                    multiline
                    label="Thumbnail"
                    rowsMax="5"
                  value={this.state.thumbnail}
                    onChange={this.handleChange('thumbnail')}
                    margin="normal"
                />
                  <div> 
                Is it a Google Drive Link ? <Checkbox
                  checked={this.state.thgLink}
                     onChange={this.handleCheckbox('thgLink')}
                      inputProps={{ 'aria-label': 'Google Link ?' }}
                   />

           </div>
                </div>
                </div>
                <FormControl className="dropdown">
                       <InputLabel>Category</InputLabel>
                      <Select
                      value={this.state.catId||''}
                      labelId="demo-simple-select-label"
                      onChange={this.handleChange('catId')}
                  >
                                   {this.state.categoryList.map((cat,i)=>(
                           <MenuItem value={cat.catId} key={i}>{cat.catName}</MenuItem>
                       ))}

                  </Select>
                  </FormControl>
              </div>
              :<div>

              {this.state.published==false && (this.state.taken==null || this.state.taken==false) ?
                <div className="binf" >
                  
                 NOT YET ASSIGNED TO TEACHER &nbsp; <Button color="primary" variant="contained" onClick={()=>{this.changeStatus()}}>
                      ALLOW TO DO CHANGES</Button>
               </div> :<div className="binf">

                   Already Assigned To A Teacher &nbsp;{this.state.teacher.name}
               </div>
               }

                   <TextField
                       label="Topic"
                       className="topic"
                       multiline
                       rowsMax="5"
                       value={this.state.topic}
                       margin="normal"
                   />
                       <div className="blgflex">
                
                <div className="blgimgc" >
                {this.state.thgLink==true?
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.gthumbnail||defaultImage} />:
                       <img  onError={(e)=>{e.target.onerror=null;e.target.src=defaultImage}}
                       src={this.state.thumbnail||defaultImage} />
                     
                     }
                     </div>

                   
                <div className="thb">
              <TextField
                    className="topic"
                    multiline
                    label="Thumbnail"
                    rowsMax="5"
                  value={this.state.thumbnail}
                  
                    margin="normal"
                />


                </div>
                </div>
                   <FormControl className="dropdown">
                       <InputLabel>Category</InputLabel>
                      {this.state.allowEdit==true?
                      <Select
                      value={this.state.catId||''}
                      labelId="demo-simple-select-label"
                      onChange={this.handleChange('catId')}
                  >
                                   {this.state.categoryList.map((cat,i)=>(
                           <MenuItem value={cat.catId} key={i}>{cat.catName}</MenuItem>
                       ))}

                  </Select>:null
                    }   {this.state.allowEdit==false?
                        <Select
                        value={this.state.catId||''}
                        labelId="demo-simple-select-label"
                    >

                                {this.state.categoryList.map((cat,i)=>(
                           <MenuItem value={cat.catId} key={i}>{cat.catName}</MenuItem>
                       ))}
                    </Select>:null
                      } 
                   </FormControl>
               </div>}
               
               <BlogContent content={this.state.content} handleDelete={this.handleDelete.bind(this)}
                handleEdit={this.handleEdit.bind(this)}/>
               <div>
                 {
                     this.state.allowEdit==true?
                     <div>
                     <Button color="primary" variant="contained"
                      onClick={this.submit.bind(this)}
                          style={{ margin: '20px' }}>
                          Submit
                      </Button> &nbsp;
                      {this.state.published==false?
                        <Button color="secondary" variant="contained"
                        onClick={this.publish.bind(this)}
                            style={{ margin: '20px' }}>
                            Publish
                        </Button>:null
                      }
                    
                     </div>:null
                 }
                
                   
               </div>
               <EditorComponent handleEditor={this.handleEditor.bind(this)}/>
               
           </div>:
              <div style={newcll.flc}>
             <div style={newcll.bl}>
             <CircularProgress color="secondary" />
             </div>

                  </div>
           }
                
              
            </div>
        


        )
    }
}

export default withSnackbar(BlogComponent)
